/* styles/globals.css */
/* Inside your component or global CSS file */



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 1.5;
}

html, body {
  height: 100%;
  margin: 0;
}

.container {
  display: flex;
  height: 100vh;
}

.left-side {
  flex: 1;
  /* background: url('https://wallpapers.com/images/hd/1920-x-1080-hd-1qq8r4pnn8cmcew4.jpg') no-repeat center center/cover; */

}

.right-side {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-container {
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: opacity 0.5s ease;
}

button {
  transition: background-color 0.3s;
}

button:hover {
  background-color: #5a67d8; /* Slightly darker shade */
}

.hidden {
  display: none;
}




/* Fade-in effect */
.fade-in {
  opacity: 0;
  animation: fadeIn 2s forwards;
}
.scroll-container {
  min-height: 100vh; /* Ensure this element takes up the full viewport height */
  overflow-y: auto; /* Ensure scrolling */
}

/* Parallax Effect Wrapper */
.parallax-wrapper {
  position: relative;
  overflow: hidden;
  height: 100vh; /* The height of this section will take the full screen */
  background-image: url('/image8.png'); /* Add your background image */
  background-size: cover;
  background-attachment: fixed; /* Parallax effect */
  background-position: center;
  background-repeat: no-repeat;
}

/* Content inside Parallax Section */
.content-scrollable {
  position: relative;
  z-index: 10; /* Keep content above the background */
  padding: 60px 20px; /* Padding for spacing */
  overflow-y: auto;
  height: 100%; /* Make content scrollable */
}

/* Optional: Adding a smooth scrolling effect */
.content-scrollable {
  scroll-behavior: smooth;
}

.social-icon {
  background: white;
  color: #1a202c;
  padding: 10px;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}
.social-icon:hover {
  color: white;
  background: #3182ce;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

